#modalButton {
    background-color: #ddd;
    border: none;
    color: black;
    padding: 16px 32px;
    text-align: center;
    font-size: 30px;
    margin: 4px 2px;
    -webkit-transition: 0.6s;
    -o-transition: 0.6s;
    transition: 0.6s;
    font-family: Arial, Helvetica, sans-serif
}

#modalButton:hover {
    background-color: #6a0b96;
    color: white;
}


